import OpenAI from 'openai';
import { projects } from '../../data/projects';

const openai = new OpenAI({
    apiKey: process.env.OPENROUTER_API_KEY,
    baseURL: 'https://openrouter.ai/api/v1',
});

function formatProjectData(projects) {
    return projects.map(p => {
        return `ğŸ“Œ ${p.name}  
ğŸ”¹ ê¸°ìˆ  ìŠ¤íƒ: ${p.stack.join(', ')}  
ğŸ”¹ ì—­í• : ${p.role}  
ğŸ”¹ ê¸°ê°„: ${p.period}  
ğŸ”¹ ì£¼ìš” ê¸°ëŠ¥: ${p.features.join(', ')}  
`;
    }).join('\n\n');
}

export async function POST(req) {
    try {
        const { messages } = await req.json();

        const completion = await openai.chat.completions.create({
            model: 'openai/gpt-3.5-turbo',
            stream: true,
            messages: [
                {
                    role: 'system',
                    content: `ë‹¹ì‹ ì€ ê°œë°œì ìœ¤ì§€ìˆ˜ì˜ AI ë¹„ì„œì…ë‹ˆë‹¤. ë‹¤ìŒ í”„ë¡œì íŠ¸ì— ëŒ€í•œ ì§ˆë¬¸ì—ë§Œ ë‹µë³€í•˜ì„¸ìš”:\n\n${formatProjectData(projects)}\n\në‹¤ë¥¸ ì£¼ì œì—ëŠ” ì‘ë‹µí•˜ì§€ ë§ˆì„¸ìš”.`,
                },
                ...messages,
            ],
        });

        const encoder = new TextEncoder();
        const stream = new ReadableStream({
            async start(controller) {
                for await (const chunk of completion) {
                    const content = chunk.choices?.[0]?.delta?.content;
                    if (content) {
                        controller.enqueue(encoder.encode(content));
                    }
                }
                controller.close();
            },
        });

        return new Response(stream, {
            headers: {
                'Content-Type': 'text/plain; charset=utf-8',
            },
        });
    } catch (err) {
        console.error('[GPT API Error]', err);
        return new Response('GPT í˜¸ì¶œ ì˜¤ë¥˜: ' + err.message, { status: 500 });
    }
}
